function Center(props) {
    const getCenter = (map) => {
        return map.map(item => {
            return {
                name: item.properties.name,
                value: item.properties.center.concat(Math.floor(Math.random() * 50000)),
            };
        });
    }
    useEffect(() => {

        const center = document.querySelector(".center");
        const e = echarts.init(center, 'dark');
        e.showLoading();
        fetch("https://geo.datav.aliyun.com/areas_v2/bound/530000_full.json").then(response => response.json()).then(res => {
            echarts.registerMap('YunNan', res);
            e.hideLoading();
            //取每个城市中心经纬度
            let data = getCenter(res.features);
            e.setOption({
                tooltip:{
                    show:true,
                    formatter:"{a}-{b}-{c}"
                },
                series: [{
                    type: 'map',
                    name: '热力图',
                    mapType: "YunNan",
                    roam: false,
                    zoom: .9,
                    aspectScale: 1,
                    left: 1,
                    right: 1,
                    top: 1,
                    bottom: 1,
                    label: {
                        show: true,
                        formatter:"{a}-{b}-{c}"
                    },
                    // 数据格式跟在 geo 坐标系上一样，每一项都是 [经度，纬度，数值大小，其它维度...]
                    data: data,
                }],
                visualMap: {
                    min: 800,
                    max: 50000,
                    formatter: "aaaa{value}bbbb{value2}",
                    text: ['High', 'Low'],
                    realtime: false,
                    calculable: true,
                    inRange: {
                        color: ['lightskyblue', 'yellow', 'orangered']
                    }
                },
            });
        });
    });

    return <div className='center'></div>
}

